/* Global Styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .project-page {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
    line-height: 1.6;
    color: #333;
    background: #fff;
  }
  
  .section-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .section-header {
    text-align: center;
    margin-bottom: 30px;
    margin-top: 30px;
  }
  
  .section-header h2 {
    font-size: 3rem;
    font-weight: 700;
    margin-bottom: 20px;
    background: linear-gradient(135deg, #667eea 0%, #9aabf7 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  
  .section-header p {
    font-size: 1.2rem;
    color: #666;
    max-width: 600px;
    margin: 0 auto;
  }
  
  /* Hero Section */
  .hero-section {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  }
  
  .hero-background {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.1;
  }
  
  .floating-elements {
    position: absolute;
    width: 100%;
    height: 100%;
  }
  
  .floating-element {
    position: absolute;
    width: 200px;
    height: 200px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 50%;
    animation: float 6s ease-in-out infinite;
  }
  
  .floating-element:nth-child(1) {
    top: 20%;
    left: 10%;
    animation-delay: 0s;
  }
  
  .floating-element:nth-child(2) {
    top: 60%;
    right: 20%;
    animation-delay: 2s;
  }
  
  .floating-element:nth-child(3) {
    bottom: 20%;
    left: 50%;
    animation-delay: 4s;
  }
  
  @keyframes float {
    0%, 100% { transform: translateY(0) rotate(0deg); }
    50% { transform: translateY(-20px) rotate(180deg); }
  }
  
  .hero-content {
    text-align: center;
    color: white;
    z-index: 2;
    max-width: 900px;
    padding: 0 20px;
  }
  
  .badge {
    display: inline-block;
    padding: 8px 20px;
    background: rgba(255, 255, 255, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 50px;
    font-size: 0.9rem;
    font-weight: 600;
    margin-bottom: 30px;
    backdrop-filter: blur(10px);
  }
  
  .hero-title {
    font-size: 4rem;
    font-weight: 800;
    line-height: 1.1;
    margin-bottom: 30px;
    animation: fadeInUp 1s ease-out;
  }
  
  .highlight {
    background: linear-gradient(45deg, #ff6b6b, #4ecdc4);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  
  .hero-subtitle {
    font-size: 1.3rem;
    margin-bottom: 30px;
    opacity: 0.9;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    animation: fadeInUp 1s ease-out 0.2s both;
  }
  
  .hero-authors {
    font-size: 1.1rem;
    margin-bottom: 40px;
    opacity: 0.8;
    animation: fadeInUp 1s ease-out 0.4s both;
  }
  
  .hero-buttons {
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
    animation: fadeInUp 1s ease-out 0.6s both;
  }
  
  .btn {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    padding: 15px 30px;
    text-decoration: none;
    border-radius: 50px;
    font-weight: 600;
    font-size: 1rem;
    transition: all 0.3s ease;
    border: none;
    cursor: pointer;
  }
  
  .btn-primary {
    background: rgba(255, 255, 255, 0.2);
    color: white;
    border: 2px solid rgba(255, 255, 255, 0.3);
    backdrop-filter: blur(10px);
  }
  
  .btn-primary:hover {
    background: rgba(255, 255, 255, 0.3);
    transform: translateY(-2px);
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  }
  
  .btn-secondary {
    background: transparent;
    color: white;
    border: 2px solid rgba(255, 255, 255, 0.3);
  }
  
  .btn-secondary:hover {
    background: rgba(255, 255, 255, 0.1);
    transform: translateY(-2px);
  }
  
  /* Results Section */
  .results-section {
    padding: 120px 0;
    background: #f8f9fa;
  }
  
  .results-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 30px;
  }
  
  .result-card {
    background: white;
    padding: 40px 30px;
    border-radius: 20px;
    text-align: center;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    position: relative;
    overflow: hidden;
  }
  
  .result-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(45deg, #667eea, #764ba2);
  }
  
  .result-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
  }
  
  .result-icon {
    font-size: 3rem;
    margin-bottom: 20px;
  }
  
  .result-value {
    font-size: 3rem;
    font-weight: 800;
    color: #667eea;
    margin-bottom: 10px;
  }
  
  .result-label {
    font-size: 1.2rem;
    font-weight: 600;
    color: #333;
    margin-bottom: 10px;
  }
  
  .result-desc {
    color: #666;
    font-size: 0.95rem;
  }
  
  /* Demo Section */
  .demo-section {
    padding: 120px 0;
    background: white;
  }
  
  .demo-tabs {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-bottom: 60px;
    flex-wrap: wrap;
  }
  
  .tab {
    padding: 12px 24px;
    background: transparent;
    border: 2px solid #e9ecef;
    border-radius: 50px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 500;
    transition: all 0.3s ease;
    color: #666;
  }
  
  .tab.active {
    background: linear-gradient(45deg, #667eea, #764ba2);
    color: white;
    border-color: transparent;
  }
  
  .tab:hover:not(.active) {
    border-color: #667eea;
    color: #667eea;
  }
  
  .segmentation-demo-card {
    background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
    border-radius: 20px;
    padding: 40px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  }
  
  .demo-header {
    text-align: center;
    margin-bottom: 40px;
  }
  
  .demo-header h3 {
    font-size: 2rem;
    margin-bottom: 10px;
    color: #333;
  }
  
  .demo-header p {
    color: #666;
    font-size: 1.1rem;
  }
  
  .before-after-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 40px;
    margin-bottom: 40px;
  }
  
  .image-container {
    flex: 1;
    position: relative;
  }
  
  .demo-image {
    width: 100%;
    height: 300px;
    border-radius: 15px;
    position: relative;
    overflow: hidden;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
  }
  
  .image-placeholder {
    width: 100%;
    height: 100%;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-weight: 600;
    font-size: 1.1rem;
    position: relative;
  }
  
  .image-placeholder.segmented {
    background: linear-gradient(135deg, #4ecdc4 0%, #44a08d 100%);
  }
  
  .image-label {
    z-index: 2;
    position: relative;
  }
  
  .scanning-line {
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
    animation: scan 3s ease-in-out infinite;
  }
  
  @keyframes scan {
    0% { left: -100%; }
    100% { left: 100%; }
  }
  
  .segmentation-mask {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 60%;
    height: 40%;
    background: rgba(255, 255, 255, 0.3);
    border: 2px solid rgba(255, 255, 255, 0.7);
    border-radius: 20px;
    transform: translate(-50%, -50%);
    animation: pulse-mask 2s ease-in-out infinite;
  }
  
  @keyframes pulse-mask {
    0%, 100% { transform: translate(-50%, -50%) scale(1); opacity: 0.7; }
    50% { transform: translate(-50%, -50%) scale(1.05); opacity: 1; }
  }
  
  .interaction-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 3;
  }
  
  .interaction-type.scribble .scribble-line {
    position: absolute;
    top: 40%;
    left: 30%;
    width: 40%;
    height: 20%;
    border: 3px solid #ff6b6b;
    border-radius: 20px;
    animation: drawScribble 2s ease-in-out infinite;
  }
  
  @keyframes drawScribble {
    0% { 
      clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
    }
    100% { 
      clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
    }
  }
  
  /* Interactive Demo Animations */
  @keyframes pulse {
    0% {
      transform: translate(-50%, -50%) scale(1);
      box-shadow: 0 0 0 0 currentColor;
    }
    70% {
      transform: translate(-50%, -50%) scale(1.1);
      box-shadow: 0 0 0 10px transparent;
    }
    100% {
      transform: translate(-50%, -50%) scale(1);
      box-shadow: 0 0 0 0 transparent;
    }
  }
  
  /* Architecture Animation Pulse */
  @keyframes architecturePulse {
    0% {
      transform: scale(1);
      box-shadow: 0 8px 25px rgba(59, 130, 246, 0.4), 0 0 20px rgba(59, 130, 246, 0.2);
    }
    50% {
      transform: scale(1.02);
      box-shadow: 0 10px 30px rgba(59, 130, 246, 0.5), 0 0 25px rgba(59, 130, 246, 0.3);
    }
    100% {
      transform: scale(1);
      box-shadow: 0 8px 25px rgba(59, 130, 246, 0.4), 0 0 20px rgba(59, 130, 246, 0.2);
    }
  }
  
  /* Progress Indicator Pulse */
  @keyframes indicatorPulse {
    0% {
      transform: scale(1);
      box-shadow: 0 0 12px rgba(59, 130, 246, 0.4);
    }
    50% {
      transform: scale(1.1);
      box-shadow: 0 0 16px rgba(59, 130, 246, 0.6);
    }
    100% {
      transform: scale(1);
      box-shadow: 0 0 12px rgba(59, 130, 246, 0.4);
    }
  }
  
  @keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-10px);
    }
    60% {
      transform: translateY(-5px);
    }
  }
  
     @keyframes dash {
     0% {
       stroke-dashoffset: 20;
     }
     100% {
       stroke-dashoffset: 0;
     }
   }
   
   @keyframes bboxDraw {
     0% {
       transform: translate(-50%, -50%) scale(0);
       opacity: 0;
     }
     25% {
       transform: translate(-50%, -50%) scale(1, 0.1);
       opacity: 1;
     }
     50% {
       transform: translate(-50%, -50%) scale(1, 0.3);
       opacity: 1;
     }
     75% {
       transform: translate(-50%, -50%) scale(1, 0.8);
       opacity: 1;
     }
     100% {
       transform: translate(-50%, -50%) scale(1);
       opacity: 1;
     }
   }
   
   @keyframes scribbleDraw {
     0% {
       stroke-dashoffset: 100%;
     }
     70% {
       stroke-dashoffset: 0%;
     }
     100% {
       stroke-dashoffset: 0%;
     }
   }
   
   @keyframes scribbleGlow {
     0%, 100% {
       filter: drop-shadow(0 0 3px currentColor);
       transform: translate(-50%, -50%) scale(1);
     }
     50% {
       filter: drop-shadow(0 0 8px currentColor);
       transform: translate(-50%, -50%) scale(1.02);
     }
   }
   
   @keyframes lassoFloat {
     0%, 100% {
       transform: translate(-50%, -50%) scale(1);
     }
     50% {
       transform: translate(-50%, -50%) scale(1.02);
     }
   }

  /* Footer */
  .footer {
    padding: 80px 0;
    background: #f8f9fa;
  }
  